.button{background:none;padding:0;border:none;width:100%;cursor:pointer;text-align:start;outline:none;display:flex;align-items:center}.label{flex-grow:1}.icon{margin-inline-start:var(--bui_spacing_4x);flex-shrink:0;transition:transform var(--bui_animation_press)}.collapser{height:0;transition:height var(--bui_animation_appear)}.button+.collapser>.content{padding-block-start:var(--bui_spacing_4x)}.root--active>.button>.icon{transform:rotate(180deg)}.root--static>.button{cursor:default}.root--static>.button>.icon{visibility:hidden}